import React, { PropTypes } from 'react';
import '../../css/common/shangpincard.css';

class ShangPinCard extends React.Component {
  render () {
    return(
      <div className="list-wrap">
        <a href={this.props.href} className="clearfix">
          <img src={this.props.img}/>
          <div >
            <p className="info">{this.props.info}</p>
            <p className="jiaqian">
              <span className="xianjia">{this.props.xianjia}</span>
              <span className="yuanjia">{this.props.yuanjia}</span>
            </p>
          </div>
        </a>
      </div>
    )
  }
}

export default ShangPinCard;



