import React, { PropTypes } from 'react'

class TitleBar extends React.Component {
  render () {
    let styles={
      h3:{
        width:"100%",
        maxWidth:"600px",
        textAlign:"center",
        fontSize:"0.14rem",
        height:"0.4rem",
        backgroundColor:"#fff",
        margin:"0",
        lineHeight:"0.4rem"

      }
    }
    return(
      <div>
        <h3 style={styles.h3}>{this.props.title}</h3>
      </div>
    )
  }
}

export default TitleBar;



