import React, { PropTypes } from 'react';
import NavigationBar from './share/NavigationBar';
import Banner from './home/Banner';
import Fenlei from './home/Fenlei';
import ChaoWan from './home/ChaoWan';
import HotZhouBian from './home/HotZhouBian';
import YouCanYouDo from './home/YouCanYouDo';
import ChaoWanBiBei from './home/ChaoWanBiBei';
import Footer from './home/Footer';
import TabBar from './share/TabBar';





class Home extends React.Component {
  render () {
    let styles={
      main:{
        width:"100%",
        maxWidth:"600px",
        margin:"0 auto",
        paddingTop:"0.44rem",
        paddingBottom:"0.48rem"
      }
    }
    return(
      <div>
          <NavigationBar />
          <div style={styles.main}>
            <Banner />
            <Fenlei />
            <ChaoWan />
            <HotZhouBian />
            <YouCanYouDo />
            <ChaoWanBiBei />
            <Footer />
          </div>
          <TabBar />
      </div>



    )
  }
}

export default Home;



