import React, { PropTypes } from 'react';
import '../../css/common/card.css';

class Card extends React.Component {
  render () {
    return(
      <div className="root">
        <a href={this.props.href} className="clearfix">
          <img src={this.props.imgSrc}/>
        </a>
        <div className="clearfix"></div>
        <div className="title-wrap clearfix" style={this.props.titleStyle}>
          <a href={this.props.href} className="title">{this.props.title}</a>
        </div>
        <p>{this.props.miaoshu}</p>
        <div className="line"></div>
      </div>
    )
  }
}

export default Card;



