import React, { PropTypes } from 'react'

class Button extends React.Component {
  render () {
    let styles={
      width:"0.87666rem",
      height:"0.293rem",
      padding:"0.08rem 0.16rem",
      paddingTop:"0.1rem",
      borderRadius:"2px",
      backgroundColor:"#ffd800",
      color:"#1c1f26",
      textAlign:"center",
      fontSize:"0.14rem",
      fontWeight:"800",
      textDecoration: "none"
    }
    return(
      <div style={this.props.style}>
          <a href={this.props.href} style={styles}>{this.props.title}</a>
      </div>
    )
  }
}

export default Button;



