import React, { PropTypes } from 'react';
import TitleBar from '../common/TitleBar';
import Card from '../common/Card';
import chaowan from '../../images/bigTop_gemini.png';
import Button from '../common/Button';
class ChaoWan extends React.Component {
  render () {
    let styles={
      root:{
        marginTop:"0.13rem",
        marginBottom:"0.13rem",
        paddingBottom:"0.13rem",
        background:"#fff"
      },
      p:{
        float:"left",
        fontSize:"0.14rem",
        color:"#494f56",
        marginLeft:"0.1rem",
        marginBottom:"0",
        marginTop:"0.3rem"
      }
    }
    return(
      <div style={styles.root} className="clearfix">
        <TitleBar title="潮玩预售"/>
        <Card imgSrc={chaowan}
              href="http://morethan.cc/wap/good.php?id=433"
              titleStyle={{display:"none"}}
              miaoshu="独家定制《胜利熊猫十二星座系列》双子座精品手办，火热预售中！魔赞联合胜利熊猫推出定制版十二星座系列手办，以活力又经典的胜利熊猫形象为载体，融合十二星座元素，赋予了每一个手办不同的性格角色。首发推出双子座手办，双重性格矛盾又和谐..."/>

        <p style={styles.p}>预售截止日:2016.10.10</p>
        <Button href="http://morethan.cc/wap/good.php?id=433" title="预购 ￥199"
        style={{float:"right",marginTop:"-0.52rem",marginRight:"0.1rem"}}/>
      </div>
    )
  }
}

export default ChaoWan;



