import React, { PropTypes } from 'react';
import '../../css/share/navigationbar.css';
import $ from 'jquery'

class NavigationBar extends React.Component {
  constructor(props){
    super(props);
    this.state={
      keyword:""
    }
  }
  onFocus(){
    $(".search .search-icon").css({'display':'none'})
    $("#search").val(" ")
                .css({'paddingLeft':'0.1rem'})

  }
  onBlur(){
    $(".search .search-icon").css({'display':'block'})
    $("#search").css({'paddingLeft':'0.96rem'}).val("请输入关键词")
  }
  oninput(){
    let input = document.getElementById('search');
    let keyword = input.value;
    this.setState({
      keyword:keyword
    })
    console.log(this.state.keyword);
  }
  render () {
    let url = "http://morethan.cc/wap/category.php?keyword=" + this.state.keyword;
    console.log(url);
    return(
      <div className="nav-bar">
        <nav className="clearfix">
          <div className="logo"><a href="http://morethan.cc/wap/index.php"></a></div>
          <div className="search">
            <form action={url} method="get">
              <input id="search" type="search" placeholder="请输入关键词"
                 onFocus={this.onFocus.bind(this)}
                 onInput={this.oninput.bind(this)}
                 onBlur={this.onBlur.bind(this)}
                 />
            </form>
            <i className="search-icon"></i>
          </div>
          <div className="user"><a href="http://morethan.cc/wap/my.php"></a></div>
        </nav>
      </div>
    )
  }
}

export default NavigationBar;



