import React, { PropTypes } from 'react';
import adbanner from '../../images/ad_banner.png';

class Banner extends React.Component {
  render () {
    let styles={
      root:{
        width:"100%",
        maxWidth:"600px"
      },
      img:{
        width:"100%"
      },
      a:{
        display:"block"
      }

    }
    return(
      <div style={styles.root} className='clearfix'>
        <a href="#" style={styles.a}><img style={styles.img} src ={adbanner}/></a>
      </div>
    )
  }
}

export default Banner;



