import React, { PropTypes } from 'react'
import TitleBar from '../common/TitleBar';
import img from '../../images/image_allMyLife.png';
import ShangPinCard from '../common/ShangPinCard';
import '../../css/home/chaowanbibei.css';
import data from '../../data/shangpin.js';

class ChaoWanBiBei extends React.Component {
  render () {
    let shangpinlist= [];
    for(let i = 0 ; i < data.length; i++){
      shangpinlist.push(
        <ShangPinCard img={data[i].img}
                      href={data[i].href}
                      info={data[i].info}
                      xianjia={data[i].xianjia}
                      yuanjia={data[i].yuanjia}
                      key={i}/>
      )
    }
    return(
      <div>
        <TitleBar title="潮玩必备"/>
        <img src={img} style={{width:"100%"}}/>
        <div className="shangpin-list clearfix">

           {shangpinlist}

        </div>
      </div>
    )
  }
}

export default ChaoWanBiBei;



