import React, { PropTypes } from 'react';
import home from '../../images/tabBar/icon_home_active.png';
import activity from '../../images/tabBar/icon_activity.png';
import brand from '../../images/tabBar/icon_brand.png';
import user from '../../images/tabBar/icon_user_tab.png';
import '../../css/share/tabbar.css'



class TabBar extends React.Component {
  render(){
    return(
      <div className="tab-bar">
        <a href="http://morethan.cc/wap/index.php">
          <img src={home} />
          首页
        </a>
        <a href="#">
          <img src={activity} />
          活动
        </a>
        <a href="http://morethan.cc/wap/brand.php">
          <img src={brand} />
          品牌
        </a>
        <a href="http://morethan.cc/wap/my.php">
          <img src={user} className="user"/>
          个人中心
        </a>
      </div>
    )
  }
}

export default TabBar;



